import React, { Component } from 'react';
import { Row, Col, Form, Checkbox } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { SelectAjax } from 'components/common';
import { requiredRule } from 'utils/validateRules';
import { getTrainTypes } from 'services/trainType';
import { getYearEducations } from 'services/yearEducationService';
import { getCourses } from 'services/course';
import { getSemesters } from 'services/semesterService';
import { getFeeObjects } from 'services/feeObjectService';
import { getResgisterSessionSelect } from 'services/registerSession';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  // getRegisterSession = () => {
  //   const data = {
  //     currentPage: 1,
  //     isEmpty: false,
  //     isNotEmpty: true,
  //     items: [
  //       {
  //         id: '1',
  //         label: '1',
  //       },
  //       {
  //         id: '2',
  //         label: '2',
  //       },
  //       {
  //         id: '3',
  //         label: '3',
  //       },
  //       {
  //         id: '4',
  //         label: '4',
  //       },
  //     ],
  //     resultsPerPage: 10,
  //     totalPages: 1,
  //     totalResults: 6,
  //   };
  //   const res = {
  //     data,
  //   };
  //   return res;
  // };

  getYearEducationsCustom = async params => {
    const res = await getYearEducations(params);
    return this.updateIdToString(res);
  };

  getCoursesCustom = async params => {
    const res = await getCourses(params);
    return this.updateIdToString(res);
  };

  getSemestersCustom = async params => {
    const res = await getSemesters(params);
    return this.updateIdToString(res);
  };

  getFeeObjectsCustom = async params => {
    const res = await getFeeObjects(params);
    return this.updateIdToString(res);
  };

  updateIdToString = res => {
    const newItems = res.data.items.map(value => {
      const newValue = value;
      newValue.id = value.id.toString();
      return newValue;
    });
    res.data.items = newItems;
    return res;
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const isSecondSF = getFieldValue('isSecondSF') || true;
    const isRestudy = getFieldValue('isRestudy') || true;
    const isRemit = getFieldValue('isRemit') || true;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    const trainTypeId = getFieldValue('trainTypeId');
    const yearEduId = getFieldValue('yearEduId');
    const courseId = getFieldValue('courseId');
    const semesterId = getFieldValue('semesterId');

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <Col span={7}>
            <Form.Item {...formItemLayout} label="Hệ đào tạo" required>
              {getFieldDecorator('trainTypeId', { rules: [requiredRule] })(
                <SelectAjax service={getTrainTypes} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item {...formItemLayout} label="Năm học" required>
              {getFieldDecorator('yearEduId', { rules: [requiredRule] })(
                <SelectAjax
                  service={this.getYearEducationsCustom}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item {...formItemLayout} label="Khóa học">
              {getFieldDecorator('courseId', {})(
                <SelectAjax service={this.getCoursesCustom} params={{ trainTypeId }} />
              )}
            </Form.Item>
          </Col>
          <Col span={3}>
            <Form.Item {...formItemLayout} label="Học kì" required>
              {getFieldDecorator('semesterId', { rules: [requiredRule] })(
                <SelectAjax service={this.getSemestersCustom} params={{ yearEduId }} />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item {...formItemLayout} label="Đợt" required>
              {getFieldDecorator('registerSessionId', { rules: [requiredRule] })(
                // <SelectAjax service={this.getRegisterSession} />
                <SelectAjax
                  service={getResgisterSessionSelect}
                  params={{ trainTypeId, courseId, yearEduId, semesterId, fullFormat: true }}
                  allowGetData={!!trainTypeId && !!courseId && !!yearEduId && !!semesterId}
                  keyValue="value"
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={8}>
            <Form.Item {...formItemLayout} label="Đối tượng học phí">
              {getFieldDecorator('fOId', {})(<SelectAjax service={this.getFeeObjectsCustom} />)}
            </Form.Item>
          </Col>
          <Col span={13}>
            <Row>
              <Col span={7} style={{ textAlign: 'center' }}>
                <Form.Item {...formItemLayout} label="Lọc theo điều kiện">
                  {getFieldDecorator('isSecondSF', {
                    valuePropName: 'checked',
                    initialValue: isSecondSF,
                  })(<Checkbox>Ngành 2</Checkbox>)}
                </Form.Item>
              </Col>
              <Col span={4} style={{ textAlign: 'center' }}>
                <Form.Item {...formItemLayout}>
                  {getFieldDecorator('isRestudy', {
                    valuePropName: 'checked',
                    initialValue: isRestudy,
                  })(<Checkbox>Học lại, cải thiện</Checkbox>)}
                </Form.Item>
              </Col>
              <Col span={4} style={{ textAlign: 'center' }}>
                <Form.Item {...formItemLayout}>
                  {getFieldDecorator('isRemit', {
                    valuePropName: 'checked',
                    initialValue: isRemit,
                  })(<Checkbox>Được miễn giảm</Checkbox>)}
                </Form.Item>
              </Col>
              <Col span={9} style={{ textAlign: 'right' }}>
                <div className="btn-group">
                  <BtnSearch htmlType="submit" />
                  <BtnClear onClick={this.handleReset} />
                </div>
              </Col>
            </Row>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



