import { request } from 'utils';

const prefix = '/api/finance';

export const getCreditsPrice = values => {
  const defaultValue = {
    pageSize: 10,
    isPagingEnabled: true,
  };
  const paramSearch = {
    ...values,
    ...defaultValue,
  };
  return request({
    prefix,
    url: '/CreditPriceConfig',
    method: 'GET',
    params: { ...paramSearch },
    data: {},
  });
};

export const updateCreditsPrice = async values => {
  try {
    const res = await request({
      prefix,
      url: '/CreditPriceConfig',
      method: 'POST',
      params: {},
      data: { ...values },
    });
    return res;
  } catch (err) {
    throw err;
  }
};



