import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Form, Checkbox, message, Button } from 'antd';
import { TableDataEditRow, CartHeadAction } from 'components/common';
import InputMoney from 'components/common/input/InputMoney';
import { getQueryParams } from 'utils/urlUtil';
import { getCreditsPrice, updateCreditsPrice } from 'services/creditsPrice';
import { moneyToString } from 'utils/formatUtil';
import SearchBar from './SearchBar';

class CreditsPrice extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  columns = [
    {
      title: 'Đối tượng học phí',
      dataIndex: 'foName',
      key: 'foName',
    },
    {
      title: 'Khóa học',
      dataIndex: 'courseLabel',
      key: 'courseLabel',
    },
    {
      title: 'Học ngành 2',
      dataIndex: 'isSecondSF',
      editable: true,
      render: (text, record) => <Checkbox checked={record.isSecondSF} />,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('isSecondSF', {
            initialValue: record.isSecondSF,
          })(<Checkbox defaultChecked={record.isSecondSF} />)}
        </Form.Item>
      ),
    },
    {
      title: 'Học lại',
      dataIndex: 'isRestudy',
      editable: true,
      render: (text, record) => <Checkbox checked={record.isRestudy} />,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('isRestudy', {
            initialValue: record.isRestudy,
          })(<Checkbox defaultChecked={record.isRestudy} />)}
        </Form.Item>
      ),
    },
    {
      title: 'Được miễn',
      dataIndex: 'isRemit',
      editable: true,
      render: (text, record) => <Checkbox checked={record.isRemit} />,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('isRemit', {
            initialValue: record.isRemit,
          })(<Checkbox defaultChecked={record.isRemit} />)}
        </Form.Item>
      ),
    },
    {
      title: 'Đơn giá/1 tín chỉ',
      dataIndex: 'unitPrice',
      key: 'unitPrice',
      editable: true,
      render: text => (text ? moneyToString(text) : 0),
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('unitPrice', {
            initialValue: record.unitPrice || 0,
          })(<InputMoney />)}
        </Form.Item>
      ),
    },
    {
      title: 'Bù kinh phí',
      dataIndex: 'compensateAmount',
      key: 'compensateAmount',
      editable: true,
      render: text => (text ? moneyToString(text) : 0),
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('compensateAmount', {
            initialValue: record.compensateAmount || 0,
          })(<InputMoney />)}
        </Form.Item>
      ),
    },
    {
      title: 'Tính chất học phí',
      dataIndex: 'tinhchathocphi',
      key: 'tinhchathocphi',
      render: (text, record) => {
        const { isSecondSF, isRestudy } = record;
        if (!isSecondSF && !isRestudy) {
          return 'Học đi';
        }
        if (isSecondSF && isRestudy) {
          return 'Học lại, cải thiện điểm ngành 2';
        }
        if (isSecondSF) {
          return 'Học đi ngành 2';
        }
        return '';
      },
    },
  ];

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleCopyToNextYear = () => {
    message.success('Sao chép sang kì sau thành công');
  };

  handleSaveRowData = async rowData => {
    try {
      const { data } = await updateCreditsPrice(rowData);
      message.success('Cập nhật thông tin thành công');
      return {
        ...rowData,
        ...data,
      };
    } catch (e) {
      return null;
    }
  };

  handleSearch = async paramSearch => {
    const res = await getCreditsPrice(paramSearch);

    const newItems = res.data.items.map((value, index) => {
      const newValue = value;
      newValue.key = index;
      return newValue;
    });
    res.data.items = newItems;
    return res;
  };

  render() {
    const { paramSearch } = this.state;
    const { trainTypeId, yearEduId, semesterId, registerSessionId } = paramSearch;

    return (
      <Card>
        <CartHeadAction>
          <Button type="primary" onClick={this.handleCopyToNextYear}>
            Sao chép sang kì sau
          </Button>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <SearchBar />
          </Col>
          <Col span={24}>
            <TableDataEditRow
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={this.handleSearch}
              allowGetData={!!trainTypeId && !!yearEduId && !!semesterId && !!registerSessionId}
              columns={this.columns}
              handleSaveRowData={this.handleSaveRowData}
              rowKey="key"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <CreditsPrice />
  </Page>
);



